import {
  Component,
  OnInit,
  TemplateRef,
  ViewChild,
  AfterViewInit,
  OnDestroy,
} from "@angular/core";
import { Title } from "@angular/platform-browser";
import { ChartOptions, ChartType, ChartDataSets } from "chart.js";
import { Label, Color } from "ng2-charts";
import { DataTableDirective } from "angular-datatables";
import { ToastrService } from "ngx-toastr";
import { Subject } from "rxjs";
import { ReportsService } from "../../../shared/services/reports.service";
import { CommonService } from "../../../shared/services/common.service";
import { DatePipe } from "@angular/common";
import { ModalDirective } from "ngx-bootstrap/modal";
import { ActivatedRoute, Router } from "@angular/router";

@Component({
  selector: "app-average-product-sales",
  templateUrl: "./average-product-sales.component.html",
  styles: [
    ".chart-div {  } .chart-wrapper{ width: auto; overflow-x: scroll; }",
  ],
})
export class AverageProductSalesComponent
  implements AfterViewInit, OnDestroy, OnInit
{
  @ViewChild("addmodal") public addmodal: ModalDirective;

  @ViewChild(DataTableDirective)
  dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  reportList: Array<any>;
  productOrderList: Array<any>;
  selectedMonth: any;
  selectedYear: any;
  years: Array<any> = [];
  productDropdownData: any;
  categoryDropdownData: any;
  chartList: Array<any>;
  date: any = new Date();
  selectedFromDate: any = new Date(
    this.date.getFullYear(),
    this.date.getMonth(),
    1
  );
  selectedToDate: any = new Date();
  selectedProduct: string = "";
  selectedCategory: string = "";
  selectedSalesUser: string = "";
  datatbaleData: any;
  salesPersonList: any;
  searchValue: string = "";
  firstTime: boolean = false;

  public ChartOptions: ChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
  };

  public barChartLabels: Label[] = [
    "Garam Masal",
    "Garam Masal",
    "Garam Masal",
    "Garam Masal",
    "Garam Masal",
    "Garam Masal",
  ];
  public barChartType: ChartType = "bar";
  public barChartLegend = true;

  public barChartData: ChartDataSets[] = [
    { data: [65, 59, 80, 81, 56, 55, 40], label: "Avarage Sale" },
  ];

  public barChartColors: Color[] = [{ backgroundColor: "#f3741c" }];

  constructor(
    private titleService: Title,
    private toastr: ToastrService,
    private reportsService: ReportsService,
    public commonService: CommonService,
    private router: Router,
    private datePipe: DatePipe,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.titleService.setTitle("Sales by Item Details");
    const body = document.getElementsByTagName("body")[0];
    body.classList.add("notlink");

    this.selectedMonth = (new Date().getMonth() + 1).toString();
    this.selectedYear = new Date().getFullYear().toString();

    for (var i = 1; i <= 5; i++) {
      this.years.push((new Date().getFullYear() - i).toString());
    }

    for (var i = 0; i <= 5; i++) {
      this.years.push((new Date().getFullYear() + i).toString());
    }

    this.years = this.years.sort((n1, n2) => n1 - n2);

    this.getReportList();

    this.getProductDropdownData();

    this.getCategoryDropdownData();

    this.getSalesUsers();

    this.route.queryParams.subscribe((param) => {
      this.selectedFromDate =
        param.selectedFromDate ||
        new Date(this.date.getFullYear(), this.date.getMonth(), 1);
      this.selectedToDate = param.selectedToDate || new Date();
      this.searchValue = param.searchValue || "";
      this.selectedCategory = param.selectedCategory || "";
      this.selectedSalesUser = param.selectedSalesUser || "";
      this.selectedProduct = param.selectedProduct || "";
      this.firstTime = param.firstTime || false;
    });
  }

  ngOnDestroy(): void {
    const body = document.getElementsByTagName("body")[0];
    body.classList.remove("notlink");
    this.dtTrigger.unsubscribe();
  }

  getReportList() {
    this.dtOptions = {
      pagingType: "full_numbers",
      pageLength: 100,
      order: [2, "asc"],
      scrollY: "450px",
      scrollX: true,
      scrollCollapse: true,
      serverSide: true,
      processing: true,
      searchDelay: 1500,
      ajax: (dataTablesParameters: any, callback) => {
        dataTablesParameters.page =
          parseInt(dataTablesParameters.start) /
            parseInt(dataTablesParameters.length) +
          1;
        dataTablesParameters.limit = dataTablesParameters.length;

        if (!this.firstTime) {
          dataTablesParameters.search_value = dataTablesParameters.search.value;
          this.searchValue = dataTablesParameters.search.value;
        } else if (this.firstTime) {
          dataTablesParameters.search_value = this.searchValue;
          $(".dataTables_filter input").val(this.searchValue);
          this.firstTime = false;
        }
        // dataTablesParameters.name =
        //   dataTablesParameters.order[0].column == 0 &&
        //   dataTablesParameters.order[0].dir == "asc"
        //     ? "name"
        //     : "-name";
        // dataTablesParameters.order_quantity_recieved =
        //   dataTablesParameters.order[0].column == 1 &&
        //   dataTablesParameters.order[0].dir == "asc"
        //     ? "order_quantity_recieved"
        //     : "-order_quantity_recieved";
        // dataTablesParameters.average =
        //   dataTablesParameters.order[0].column == 2 &&
        //   dataTablesParameters.order[0].dir == "asc"
        //     ? "average"
        //     : "-average";

        var formData = new FormData();
        formData.append("page", dataTablesParameters.page.toString());
        formData.append("limit", dataTablesParameters.length);

        // filter
        this.selectedFromDate
          ? formData.append(
              "from_date",
              this.datePipe.transform(this.selectedFromDate, "yyyy-MM-dd")
            )
          : "";
        this.selectedToDate
          ? formData.append(
              "to_date",
              this.datePipe.transform(this.selectedToDate, "yyyy-MM-dd")
            )
          : "";

        formData.append("product_id", this.selectedProduct);
        formData.append("category_id", this.selectedCategory);
        formData.append("sales_user_id", this.selectedSalesUser);

        formData.append("search_param", dataTablesParameters.search_value);
        dataTablesParameters.order[0].column == 0
          ? formData.append(
              "product_item_no",
              dataTablesParameters.order[0].column == 0 &&
                dataTablesParameters.order[0].dir == "asc"
                ? "product_item_no"
                : "-product_item_no"
            )
          : "";
        dataTablesParameters.order[0].column == 1
          ? formData.append(
              "category_name",
              dataTablesParameters.order[0].column == 1 &&
                dataTablesParameters.order[0].dir == "asc"
                ? "category_name"
                : "-category_name"
            )
          : "";
        dataTablesParameters.order[0].column == 2
          ? formData.append(
              "name",
              dataTablesParameters.order[0].column == 2 &&
                dataTablesParameters.order[0].dir == "asc"
                ? "name"
                : "-name"
            )
          : "";
        dataTablesParameters.order[0].column == 3
          ? formData.append(
              "order_quantity_recieved",
              dataTablesParameters.order[0].column == 3 &&
                dataTablesParameters.order[0].dir == "asc"
                ? "order_quantity_recieved"
                : "-order_quantity_recieved"
            )
          : "";
        dataTablesParameters.order[0].column == 4
          ? formData.append(
              "average",
              dataTablesParameters.order[0].column == 4 &&
                dataTablesParameters.order[0].dir == "asc"
                ? "average"
                : "-average"
            )
          : "";

        this.datatbaleData = dataTablesParameters;

        this.reportsService.average_sales_report(formData).subscribe(
          (respones) => {
            let resData = JSON.parse(JSON.stringify(respones));
            if (resData.status_code == 200) {
              this.reportList = resData.data.products;

              // this.chartList = resData.data.chart.x;

              // this.barChartData = [
              //   {
              //     //data: Array.prototype.map.call(resData.data.products, s => s.average),
              //     data: resData.data.chart.x,
              //     label: "Avarage Sale",
              //   },
              // ];
              // //this.barChartLabels = Array.prototype.map.call(resData.data.products, s => s.name);
              // this.barChartLabels = resData.data.chart.y;
              // this.ChartOptions = {
              //   responsive: true,
              //   maintainAspectRatio: false,
              //   tooltips: {
              //     enabled: true,
              //     intersect: true,
              //     mode: "index",
              //     position: "nearest",
              //     xPadding: 20,
              //     titleMarginBottom: 10,
              //     titleFontSize: 15,
              //     titleSpacing: 10,
              //     bodyFontSize: 14,
              //   },
              //   scales: {
              //     yAxes: [
              //       {
              //         ticks: {
              //           beginAtZero: true,
              //         },
              //       },
              //     ],
              //   },
              // };

              setTimeout(() => {
                var table = $("#excel-table").DataTable();

                table.columns.adjust();

                document.getElementById("last-header").style.width = "auto";
              }, 10);

              callback({
                recordsTotal: resData.total_record,
                recordsFiltered:
                  dataTablesParameters.search_value == ""
                    ? resData.total_record
                    : resData.filter_record,
                data: [],
              });
            } else {
              this.toastr.error("", resData.message);
            }
          },
          (err) => {
            this.toastr.error("", err.error.message);
          }
        );
      },
      columns: [
        { data: "product_item_no", searchable: true, orderable: true },
        { data: "category_name", searchable: true, orderable: true },
        { data: "name", searchable: true, orderable: true },
        { data: "order_quantity_recieved", searchable: true, orderable: true },
        { data: "average", searchable: true, orderable: true },
      ],
    };
  }

  getSalesUsers() {
    var formData = new FormData();
    formData.append("page", "1");
    formData.append("limit", "10000");

    this.reportsService.userlist(formData).subscribe(
      (response) => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          this.salesPersonList = resData.data;
        } else {
          this.toastr.error("", resData.message);
        }
      },
      (err) => {
        this.toastr.error("", err.error.message);
      }
    );
  }

  ngAfterViewInit(): void {
    this.dtTrigger.next();
  }

  rerender(): void {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      dtInstance.destroy();
      this.dtTrigger.next();
    });
  }

  changeMonth() {
    this.rerender();
  }

  changeYear() {
    this.rerender();
  }

  exportTable(type) {
    var formData = new FormData();
    formData.append("page", "1");
    formData.append("limit", "10000");

    // filter
    this.selectedFromDate
      ? formData.append(
          "from_date",
          this.datePipe.transform(this.selectedFromDate, "yyyy-MM-dd")
        )
      : "";
    this.selectedToDate
      ? formData.append(
          "to_date",
          this.datePipe.transform(this.selectedToDate, "yyyy-MM-dd")
        )
      : "";

    formData.append("product_id", this.selectedProduct);
    formData.append("category_id", this.selectedCategory);
    formData.append("sales_user_id", this.selectedSalesUser);

    formData.append("search_param", this.datatbaleData.search_value);
    this.datatbaleData.order[0].column == 0
      ? formData.append("name", this.datatbaleData.name)
      : "";
    this.datatbaleData.order[0].column == 1
      ? formData.append("product_item_no", this.datatbaleData.product_item_no)
      : "";
    this.datatbaleData.order[0].column == 2
      ? formData.append(
          "order_quantity_recieved",
          this.datatbaleData.order_quantity_recieved
        )
      : "";
    this.datatbaleData.order[0].column == 3
      ? formData.append("average", this.datatbaleData.average)
      : "";
    this.reportsService.average_sales_report(formData).subscribe(
      (response) => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          const onlyNameAndSymbolArr = resData.data.products.map((x) => ({
            "Item Number": x.product_item_no || "-",
            "Category Name": x.category_name || "-",
            "Product Name": x.name || "-",
            "Number of product sold": x.order_quanity_recieved || "0",
            "Average Sale (Average = Total Sum of All products / Number of product sold)":
              x.average || "0",
          }));

          if (type == "pdf") {
            var keys = [Object.keys(onlyNameAndSymbolArr[0])];
            var values = onlyNameAndSymbolArr.map((element) =>
              Object.values(element)
            );
            this.commonService.exportArrayPDF(
              keys,
              values,
              "avarageProductList"
            );
          } else {
            this.commonService.exportCsv(
              resData.data.products,
              onlyNameAndSymbolArr,
              "avarageProductList"
            );
          }
        } else {
          this.toastr.error("", resData.message);
        }
      },
      (err) => {
        this.toastr.error("", err.error.message);
      }
    );
  }

  getProductDropdownData() {
    var formData = new FormData();
    formData.append("page", "1");
    formData.append("limit", "100000");
    this.reportsService.productlist(formData).subscribe(
      (response) => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          this.productDropdownData = resData.data;
        } else {
          this.toastr.error("", resData.message);
        }
      },
      (err) => {
        this.toastr.error("", err.error.message);
      }
    );
  }

  getCategoryDropdownData() {
    var formData = new FormData();
    formData.append("page", "1");
    formData.append("limit", "100000");
    this.reportsService.categorieslist(formData).subscribe(
      (response) => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          this.categoryDropdownData = resData.data;
        } else {
          this.toastr.error("", resData.message);
        }
      },
      (err) => {
        this.toastr.error("", err.error.message);
      }
    );
  }

  goToProductOrderPage(selectedProduct) {
    this.router.navigate(["/reports/product-order"], {
      queryParams: {
        id: selectedProduct,
        selectedFromDate: this.datePipe.transform(
          this.selectedFromDate,
          "MM/dd/yyyy"
        ),
        selectedToDate: this.datePipe.transform(
          this.selectedToDate,
          "MM/dd/yyyy"
        ),
        selectedCategory: this.selectedCategory,
        searchValue: this.searchValue,
        selectedSalesUser: this.selectedSalesUser,
        selectedProduct: this.selectedProduct,
      },
    });
  }
}
