  rerender(): void {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      dtInstance.destroy();
      this.dtTrigger.next();
    });
  }

  exportTable(type) {
    this.datatbaleData.page = "1";
    this.datatbaleData.limit = "100000";
    this.reportsService.admin_ageing_report(this.datatbaleData).subscribe(
      (response) => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          if (resData.data == 0) {
            this.toastr.error("", "No Data Found !");
          } else {
            const onlyNameAndSymbolArr = resData.data.map((x) => ({
              "Store Name": x.store_name || "-",
              "Invoice No": x.invoice_no || "-",
              "PO No": x.po_num || "-",
              "Delivery Date":
                this.datePipe.transform(x.delivery_date, "MM-dd-yyyy") || "-",
              "Invoice Date":
                this.datePipe.transform(x.invoice_date, "MM-dd-yyyy") || "-",
              "Payment Due Date":
                this.datePipe.transform(x.due_date, "MM-dd-yyyy") || "-",
              Aging: x.ageing ? (x.ageing > 0 ? x.ageing : "-") : "-",
              "Total Amount": x.total_amount ? "$" + x.total_amount : "$0",
              "Amount Received": x.amount_recieved
                ? "$" + x.amount_recieved
                : "$0",
              "Open Balance": x.open_balance ? "$" + x.open_balance : "$0",
            }));

            if (type == "pdf") {
              var keys = [Object.keys(onlyNameAndSymbolArr[0])];
              var values = onlyNameAndSymbolArr.map((element) =>
                Object.values(element)
              );
              this.commonService.exportArrayPDF(
                keys,
                values,
                "agingReportList"
              );
            } else {
              this.commonService.exportCsv(
                resData.data,
                onlyNameAndSymbolArr,
                "agingReportList"
              );
            }
          }
        } else {
          this.toastr.error("", resData.message);
        }
      },
      (err) => {
        this.toastr.error("", err.error.message);
      }
    );
  }

  SavePDF() {
    // remove action column
    this.spinner.show();
    // $("#excel-table thead tr").find('th:eq(8)').remove();
    // $("#excel-table tbody tr").each(function() {
    //   $(this).find("td:eq(8)").remove();
    // });

    // pdf export
    const doc = new jsPDF();
    autoTable(doc, {
      html: "#excel-table",
      columnStyles: { europe: { halign: "center" } },
      styles: { halign: "center", font: "times", cellPadding: 5 },
    });
    doc.save("avarageProductList.pdf");
    this.rerender();
    this.spinner.hide();
    $("#myDropdown").toggle();
  }

