import { ProductOrderComponent } from './average-product-sales/product-order/product-order.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdjustedQuantityReportComponent } from './adjusted-quantity-report/adjusted-quantity-report.component';
import { AgingReportComponent } from './aging-report/aging-report.component';
import { AverageProductHandsComponent } from './average-product-hands/average-product-hands.component';
import { AverageProductSalesComponent } from './average-product-sales/average-product-sales.component';
import { InitialQuantityReportComponent } from './initial-quantity-report/initial-quantity-report.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Reports'
    },
    children: [
      {
        path: '',
        data: {
          title: 'List'
        }
      },
      {
        path: 'initial-quantity-report',
        component: InitialQuantityReportComponent,
        data: {
          title: 'Initial Product Quantity Report'
        }
      },
      {
        path: 'adjusted-quantity-report',
        component: AdjustedQuantityReportComponent,
        data: {
          title: 'Adjusted Quantity Report'
        }
      },
      {
        path: 'sales-by-item-details',
        // path: 'average-product-sales',
        component: AverageProductSalesComponent,
        data: {
          title: 'Sales by Item Details'
          // title: 'Average Product Sales Report'
        }
      },
      {
        path: 'product-order',
        component: ProductOrderComponent,
        data: {
          title: 'Product Order Details'
        }
      },
      {
        path: 'aging-report',
        component: AgingReportComponent,
        data: {
          title: 'Aging Report'
        }
      },
      {
        path: 'average-product-hand',
        component: AverageProductHandsComponent,
        data: {
          title: 'Average Product On Hand Report'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReportsRoutingModule { }
