import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { environment } from "../../../environments/environment";

@Injectable()
export class ReportsService {
  constructor(private http: HttpClient) {}

  avarageReportList(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/salesreports/averagereport/`,
      data
    );
  }

  initial_reports(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/initial_reports/`,
      data
    );
  }

  adjust_quantity_list(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/adjust_quantity/list_view/`,
      data
    );
  }

  get_product_orders(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/average_sales_report/get_product_orders/`,
      data
    );
  }

  average_sales_report(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/average_sales_report/`,
      data
    );
  }

  admin_ageing_report(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/admin_ageing_report/`,
      data
    );
  }

  categorieslist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/categorieslist/`, data);
  }

  productlist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/productlist/`, data);
  }

  storelist(): Observable<any> {
    return this.http.get<any>(`${environment.base_url}/store_name_list/`);
  }

  average_hands_report(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/average_sales_report/`,
      data
    );
  }

  new_admin_ageing_report(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/new_admin_ageing_report/`,
      data
    );
  }

  average_onhand_product_report(data): Observable<any> {
    return this.http.post<any>(
      `${environment.base_url}/average_onhand_product_report/`,
      data
    );
  }

  userlist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/user/salesuser/`, data);
  }
}
